import React, { useRef } from "react";
import useSWR from "swr";
import TrackVisibility from "react-on-screen";
import { PRepItem, InnerLoader } from "..";
import Query from "../../apis/query";
import { PRepListItem, ProposalsStatusCnt } from "../../types";

interface Props {
  pRepName: string;
  username: string;
}

const PRepItemContainer: React.SFC<Props> = ({ pRepName, username }) => {
  const [fetchData, setFetchData] = React.useState<boolean>(false);
  const { data } = useSWR<ProposalsStatusCnt>(
    fetchData ? `get_proposals_status_cnt_${username}` : null,
    () =>
      Query.getProposalsStatusCnt({
        _proposer: username,
      }),
    {
      revalidateOnFocus: false,
      revalidateOnReconnect: false,
    }
  );

  return (
    <TrackVisibility once>
      {({ isVisible }) => (
        <PRepItem
          key={username}
          name={pRepName}
          openCnt={data ? data.openCnt : 0}
          closeCnt={data ? data.closeCnt : 0}
          link={`/proposals/${username}`}
          isLoading={!data}
          isVisible={isVisible}
          fetchData={() => setFetchData(true)}
        />
      )}
    </TrackVisibility>
  );
};

export default PRepItemContainer;
