import React from "react";
import { Link, useParams } from "react-router-dom";
import { createUseStyles } from "react-jss";
import { ThemeType } from "../styles/theme";
import Query from "../apis/query";
import {
  Title,
  Breadcrumb,
  ProposalListContainer,
  VoteCardList,
  Loader,
} from "../components";
import Mock from "../constants/mock";
import useSWR from "swr";

const useStyles = createUseStyles((theme: ThemeType) => ({
  main: {
    marginTop: 30,
  },
  main__title: {
    display: "flex",
    alignItems: "baseline",
    marginBottom: 30,
  },
  proposals: {},
  votes: {},
}));

const PRepDetailPage: React.SFC = () => {
  const classes = useStyles();
  const { username } = useParams();
  const { data } = useSWR(
    `get_prep_by_id_${username}`,
    () =>
      Query.getPRepInfoById({
        _id: username!,
      }),
    {}
  );

  if (!data) {
    return <Loader height={600} />;
  }

  const { name } = data;

  return (
    <div className="container">
      <div>
        <Breadcrumb>
          <Link to="/">Home</Link>
          <Link to={`/proposals/${username}`}>{name}</Link>
        </Breadcrumb>
      </div>
      <div className={classes.main}>
        <div className={classes.main__title}>
          <Title text={name} />
        </div>
        <div className={classes.proposals}>
          <ProposalListContainer username={username} hasFilter={true} />
        </div>
        <div className={classes.votes}>
          <VoteCardList votes={Mock.votes} />
        </div>
      </div>
    </div>
  );
};

export default PRepDetailPage;
