import React from "react";
import { Link, useParams } from "react-router-dom";
import { createUseStyles } from "react-jss";
import { ThemeType } from "../styles/theme";
import {
  Title,
  Breadcrumb,
  ProposalVoteSection,
  ProposalMetaData,
  ProposalContent,
  Chart,
  Loader,
} from "../components";
import Query from "../apis/query";
import { ProposalType, ProposalStatusType } from "../types";
import useSWR from "swr";

interface Props {}

const useStyles = createUseStyles((theme: ThemeType) => ({
  proposal: {
    marginTop: 28,
  },
  proposal__header: {},
  proposal__dashboard: {
    marginTop: 22,
    marginBottom: 64,
    display: "flex",
    flexWrap: "wrap",
  },
  proposal__vote: {
    width: "48%",
    marginRight: "4%",
    [theme["breakpoint-sm"]]: {
      width: "100%",
      marginRight: 0,
      marginBottom: 40,
    },
  },
  proposal__graph: {
    width: "48%",
    [theme["breakpoint-sm"]]: {
      width: "100%",
    },
  },
  proposal__description: {
    marginTop: 44,
  },
}));

const ProposalDetailPage: React.SFC<Props> = (props) => {
  const classes = useStyles();

  const { username, proposalId } = useParams();
  const { data } = useSWR(`get_proposal_${username}_${proposalId}`, () =>
    Query.getProposal({
      _proposal_id: Number(proposalId!),
      _proposer: username!,
    })
  );

  if (!data) {
    return <Loader height={600} />;
  }

  const {
    ID,
    subject,
    status,
    select_item,
    winning_threshold,
    contents,
    expire_timestamp,
  } = data;
  // TODO: P-Rep Name required in Breadcrumb

  console.log(contents);
  return (
    <div className="container">
      <div>
        <Breadcrumb>
          <Link to="/">Home</Link>
          <Link to={`/proposals/${username}`}>{username}</Link>
          <Link to={`/proposal/${username}/${ID}`}>{subject}</Link>
        </Breadcrumb>
      </div>
      <div className={classes.proposal}>
        <div className={classes.proposal__header}>
          <Title text={subject} />
          <ProposalMetaData
            status={status}
            pRepName={username}
            proposalType={ProposalType.COMMUNITY}
            endsAt={expire_timestamp}
            style={{
              fontSize: "1.6rem",
              paddingTop: 16,
            }}
          />
        </div>
        <div className={classes.proposal__dashboard}>
          <div className={classes.proposal__vote}>
            <ProposalVoteSection
              options={select_item.map((item) => ({
                name: item,
                percent: "20",
              }))}
              minApprovalRate={winning_threshold}
            />
          </div>
          <div className={classes.proposal__graph}>
            <Chart votedPct={45} voterCnt={12} requiredPct={50} />
          </div>
        </div>
        <div className={classes.proposal__description}>
          <Title text="Description" />
          <ProposalContent content={contents} />
        </div>
      </div>
    </div>
  );
};

export default ProposalDetailPage;
