import React from "react";
import useSWR from "swr";
import { Card } from "..";
import Query from "../../apis/query";
import { convertVotedPowerToPercent } from "../../utils/";
import { ProposalModel } from "../../types";

interface Props {
  proposal: ProposalModel;
}

const ProposalCardContainer: React.SFC<Props> = (props) => {
  const { proposal } = props;
  const proposalWithVoteRates = { ...proposal };
  const { data } = useSWR(
    `get_vote_rates_and_total_voted_power_${proposal.pRepName}_${proposal.id}`,
    async () =>
      await Query.getVoteRatesAndTotalVotedPower({
        _proposal_id: Number(proposal.id),
        _proposer: proposal.pRepName,
      })
  );

  if (data) {
    proposalWithVoteRates.options = data!.voteRates.map((voteRate) => ({
      name: voteRate.item,
      percent: convertVotedPowerToPercent(
        voteRate.votedPower,
        data!.totalVotedPower
      ),
    }));
  }

  return <Card item={proposalWithVoteRates} isLoading={!data} />;
};

export default ProposalCardContainer;
