import BigNumber from "bignumber.js";

export enum ProposalType {
  FANS = "Fans only",
  COMMUNITY = "Community",
}

export enum ProposalStatusType {
  VOTING = "VOTING",
  APPROVED = "APPROVED",
  REJECTED = "REJECTED",
  CANCELLED = "CANCELLED",
}

/* View Model */

export interface ProposalOptionModel {
  name: string;
  percent: string;
}

export interface ProposalModel {
  id: string;
  pRepName: string;
  title: string;
  options: ProposalOptionModel[];
  proposalType: ProposalType;
  endsAt: number;
  pRepLink: string;
  proposalLink: string;
  status: ProposalStatusType;
}

export interface VoteModel extends ProposalModel {
  selectedOption: number;
}

/* Data Model */

export interface Proposal {
  ID: string;
  address: string;
  contents: string;
  electoral_threshold: string;
  expire_timestamp: number;
  final: string;
  subject: string;
  status: ProposalStatusType;
  expire_date: string;
  select_item: string[];
  winning_threshold: number;
  transaction: string;
  winner: number;
}

export interface PRep {
  address: string;
  status: string;
  penalty: string;
  grade: string;
  name: string;
  country: string;
  city: string;
  stake: string;
  delegated: string;
  totalBlocks: string;
  validatedBlocks: string;
  unvalidatedSequenceBlocks: string;
  irep: string;
  irepUpdateBlockHeight: string;
  lastGenerateBlockHeight: string;
  blockHeight: string;
  txIndex: string;
  email: string;
  website: string;
  details: string;
  p2pEndpoint: string;
}

export interface PRepListItem {
  username: string;
  address: string;
  pRepName: string;
}

export interface Vote {
  voter: string;
  selectItem: number;
  voteTxHash: string;
  delegateTxID: string;
  delegateAmount: BigNumber;
}

export interface ProposalsStatusCnt {
  openCnt: number;
  closeCnt: number;
}
